.el-upload {
	display: inline-block;
	text-align: center;
	cursor: pointer;
	outline: 0
}
.el-upload__input {
	display: none
}
.el-upload__tip {
	font-size: 12px;
	color: #606266;
	margin-top: 7px
}
.el-upload iframe {
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	opacity: 0;
	filter: alpha(opacity=0)
}
.el-upload--picture-card {
	background-color: #fbfdff;
	border: 1px dashed #c0ccda;
	border-radius: 6px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 148px;
	height: 148px;
	line-height: 146px;
	vertical-align: top
}
.el-upload--picture-card i {
	font-size: 28px;
	color: #8c939d
}
.el-upload--picture-card:hover,
.el-upload:focus {
	border-color: #409EFF;
	color: #409EFF
}
.el-upload:focus .el-upload-dragger {
	border-color: #409EFF
}
.el-upload-dragger {
	background-color: #fff;
	border: 1px dashed #d9d9d9;
	border-radius: 6px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 360px;
	height: 180px;
	text-align: center;
	position: relative;
	overflow: hidden
}
.el-upload-dragger .el-icon-upload {
	font-size: 67px;
	color: #c0c4cc;
	margin: 40px 0 16px;
	line-height: 50px
}
.el-upload-dragger+.el-upload__tip {
	text-align: center
}
.el-upload-dragger~.el-upload__files {
	border-top: 1px solid #dcdfe6;
	margin-top: 7px;
	padding-top: 5px
}
.el-upload-dragger .el-upload__text {
	color: #606266;
	font-size: 14px;
	text-align: center
}
.el-upload-dragger .el-upload__text em {
	color: #409EFF;
	font-style: normal
}
.el-upload-dragger:hover {
	border-color: #409EFF
}
.el-upload-dragger.is-dragover {
	background-color: rgba(32, 159, 255, .06);
	border: 2px dashed #409EFF
}
.el-upload-list {
	margin: 0;
	padding: 0;
	list-style: none
}
.el-upload-list__item {
	-webkit-transition: all .5s cubic-bezier(.55, 0, .1, 1);
	transition: all .5s cubic-bezier(.55, 0, .1, 1);
	font-size: 14px;
	color: #606266;
	line-height: 1.8;
	margin-top: 5px;
	position: relative;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	border-radius: 4px;
	width: 100%
}
.el-upload-list__item .el-progress {
	position: absolute;
	top: 20px;
	width: 100%
}
.el-upload-list__item .el-progress__text {
	position: absolute;
	right: 0;
	top: -13px
}
.el-upload-list__item .el-progress-bar {
	margin-right: 0;
	padding-right: 0
}
.el-upload-list__item:first-child {
	margin-top: 10px
}
.el-upload-list__item .el-icon-upload-success {
	color: #67c23a
}
.el-upload-list__item .el-icon-close {
	display: none;
	position: absolute;
	top: 5px;
	right: 5px;
	cursor: pointer;
	opacity: .75;
	color: #606266
}
.el-upload-list__item .el-icon-close:hover {
	opacity: 1
}
.el-upload-list__item .el-icon-close-tip {
	display: none;
